body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/**
*
*/

/* board variables */
:root
{
	/* default color theme */
	--light-color: #ccc;
	--dark-color: #378a24;
	--border-color: #000;

	/* size variables */
	--cell-size: 64px;
	--border-size: 10px;
	--board-size: calc(var(--cell-size) * 8);
}

/* border wrapper setup */
.board_border 
{
	float: left;
	width: calc(calc(64px * 8) + 10px * 2);
	width: calc(var(--board-size) + var(--border-size) * 2); /* fix for border */
	height: calc(calc(64px * 8) + 10px * 2);
	height: calc(var(--board-size) + var(--border-size) * 2); /* don't know why it's working now but ok */
	border: solid #000 0;
	border: solid var(--border-color) 0;
}

/* actual board */
table#t01
{
	border-collapse: collapse;
}
table#t01 td
{
	width: 64px;
	width: var(--cell-size);
	height: 64px;
	height: var(--cell-size);
	padding: 0;
}
table#t01 tr:nth-child(odd) td:nth-child(even) { background: #378a24; background: var(--dark-color); }
table#t01 tr:nth-child(even) td:nth-child(odd) { background: #378a24; background: var(--dark-color); }
table#t01 tr:nth-child(even) td:nth-child(even) { background: #ccc; background: var(--light-color); }
table#t01 tr:nth-child(odd) td:nth-child(odd) { background: #ccc; background: var(--light-color); }

#toggle {
    cursor: pointer;
    color: #9fffcb;
    font-size: 30px;
    margin-left: 20px;
    background: transparent;
    border: none;
    padding: 10px;
}

.navbar {
    background-color: #222;
}

a,
.navbar-nav,
.navbar-light .nav-link {
    color: #9fffcb;
}
.navbar-nav:hover {
        color: white;
}


.navbar-brand {
    font-size: 1.4em;
    color: #9fffcb;
}
.navbar-brand:hover {
        color: white;
}

.form-center {
    position: absolute !important;
    left: 25%;
    right: 25%;
}

.sidenav {
    height: 100vh;
    padding-left: 0px;
    background-color: #222222;
    opacity: 1;
    color: white;
    width: 250px;
    position: fixed;
    z-index: 200;
    left: -100%;
    -webkit-animation: slide-open .75s forwards;
            animation: slide-open .75s forwards;
    float: top;
}

.sidenav.close {
    left: 0;
    -webkit-animation: slide-closed 1.0s forwards;
            animation: slide-closed 1.0s forwards;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.space {
    height: 7rem;
}


/* form */

.form label,
.form input {
  display: block;
}
.form input {
  margin-bottom: 1rem;
}
.form label {
  margin-bottom: 0.2rem;
}
.form input[type="text"],
.form input[type="email"],
.form input[type="password"] {
  width: 100%;
  padding: 0.3rem;
  border: 1px solid #c2c2c2;
  border-radius: 8px;
  background-color: #f0f0f0;
  font-size: 1.1rem;
}
.form input:focus {
  border: 1px solid #50a4e9;
  box-shadow: inset 0px 0px 5px rgba(38, 142, 228, 0.5);
  outline: none;
}
.form input[type="submit"] {
  padding: 0.3rem 0.5rem;
  border: 1px solid #6aa051;
  border-radius: 8px;
  background-color: #c8ecb7;
  font-size: 1.1rem;
}

/* todo list */

.todo-list ul {
  list-style: disc;
  cursor: pointer;
}

.todo-list li:hover {
  text-decoration: line-through;
}

/* error notice */

.error-notice {
  margin: 1rem 0;
  padding: 0.5rem;
  border: 1px solid #e07c7c;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8d6d6;
}
.error-notice button {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #df4343;
  color: #ffffff;
}

@-webkit-keyframes slide-open {
    100% {
        left: 0
    }
}

@keyframes slide-open {
    100% {
        left: 0
    }
}

@-webkit-keyframes slide-closed {
    100% {
        left: -100%
    }
}

@keyframes slide-closed {
    100% {
        left: -100%
    }
}
